const allowedHosts = [
    'localhost',
    '127.0.0.1',
    'xtpg-api.herokuapp.com'
];

if (process.env.NODE_ENV !== 'development' && !allowedHosts.filter(host => host === window.location.hostname).length) {
    routes = [{ path: '/docs', redirect: '/', }];
}

export default routes;
