import axios from 'axios';

let api, mock;

export function http(name) {
    createHttp(name);

    switch (name) {
        case 'docs':
            return mock;
        case 'api':
            return api;
        case 'axios':
            return axios;
        default:
            return api;
    }
}

function createHttp(name) {
    if (!mock && name === 'docs') {
        mock = axios.create({
            baseURL: 'https://xtpg-api.herokuapp.com',
            validateStatus: false
        });
    }
    if (!api && (!name || name === 'api')) {
        api = axios.create({
            baseURL: `${loadBaseUrl()}/api`,
            validateStatus: false
        });
    }
}

function loadBaseUrl() {
    try {
        let baseUrl = document.querySelector('meta[name="api"]').content;
        if (baseUrl !== '__API__') {
            return baseUrl;
        }
        return process.env.VUE_APP_BASE_URL;
    } catch (err) {
        return process.env.VUE_APP_BASE_URL;
    }
}

export default http;
